import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Table, Col } from 'antd';
import { setStudentCode } from 'dump/data';
// import SearchForm from './SearchForm';
import StructCodeForm from './StructCodeForm';
import FormSearch from './FormSearch';

const columns = [
  {
    title: 'Mã sv',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },

  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Giới tính',
    dataIndex: 'sex',
    key: 'sex',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'className',
  },
];

class SetStudentCodePage extends Component {
  state = {};

  render() {
    return (
      <Card title="Lập mã sinh viên">
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={setStudentCode} columns={columns} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <h5>Thiết lập cấu trúc mã sinh viên</h5>
          </Col>
          <Col span={24}>
            <StructCodeForm />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <SetStudentCodePage />
  </Page>
);



