import React, { Component } from 'react';
import { Row, Col, Form, Button, Input, Checkbox } from 'antd';

@Form.create({ name: 'formScholarshipApproval' })
class StructCodeForm extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Độ dài của số tự tăng">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Bắt đầu đánh từ số">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Phần đầu của mã">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Phần cuối của mã">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Giải mã sinh viên sẽ là">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Đến">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              <Checkbox>Sinh viên chưa có mã</Checkbox>
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              <Button type="primary" htmlType="submit">
                Lập mã tự động
              </Button>
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              <Button type="danger">Xóa tất cả mã</Button>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default StructCodeForm;



